export default {
  name: 'lbp-video',
  props: {
    src: PropTypes.string({ label: 'è§é¢å°å' }),
    disabled: PropTypes.boolean({ label: 'disabled' }),
    useIframe: PropTypes.boolean({ label: 'ä½¿ç¨iframe' }),
    iframeSrc: PropTypes.string({
      default: '',
      label: 'iframe å°å',
      props: {
        type: 'textarea',
        placeholder: 'åªæä½¿ç¨iframeæå¼çæ¶åï¼è¿ä¸ªæææ'
      },
      extra: h => {
        return 'ãä½¿ç¨iframeãæå¼çæ¶åï¼è¿ä¸ªæææï¼ä¸ä¼ è§é¢è¯·å¿½ç¥è¯¥éç½®'
      }
    })
  },
  watch: {
    src () {
      this.appendIframe()
    },
    disabled () {
      this.appendIframe()
    },
    useIframe () {
      this.appendIframe()
    },
    iframeSrc () {
      this.appendIframe()
    }
  },
  mounted () {
    this.appendIframe()
  },
  methods: {
    appendIframe () {
      if (this.useIframe && this.iframeSrc) {
        // v-html
        this.$refs.iframeWrapper &&
          (this.$refs.iframeWrapper.innerHTML = this.iframeSrc)
      }
      // else if (this.src) {
      //   this.$refs.videoTag && (this.$refs.videoTag.innerHTML = `<source type="video/mp4" src=${this.src} />`)
      // }
    }
  },
  render (h) {
    const style = this.disabled ? { 'pointer-events': 'none' } : {}
    return (
      <div class="lbc-video" style={style}>
        {this.useIframe ? (
          <div ref="iframeWrapper">
            <img src={playIcon} width="100%" height="100%" />
          </div>
        ) : (
          <video
            playsinline="true"
            webkit-playsinline=""
            width="100%"
            height="100%"
            poster={playIcon}
            ref="videoTag"
            controls
          >
            <source type="video/mp4" src={this.src} />
          </video>
        )}
      </div>
    )
  },
  componentsForPropsEditor: {}
}
