import Map from './Map'

function getPosition ({ lat, lng }) {
  return new window.qq.maps.LatLng(lat, lng) // å°å¾çä¸­å¿å°çåæ 
}

export default {
  methods: {
    loadMap (key) {
      return Map.load(key)
    },
    setMarker (poi) {
      const map = this.map
      const center = getPosition(poi.latLng) // å°å¾çä¸­å¿å°çåæ 
      if (this.marker) {
        this.marker.setMap(null)
        this.marker = new window.qq.maps.Marker({ map, position: poi.latLng })
        map.panTo(center)
      } else {
        this.marker = new window.qq.maps.Marker({ map, position: center })
      }
    }
  }
}
