// https://github.com/luban-h5-components/plugin-common-props
import PropTypes from '@/utils/commom-props'

export default {
  render () {
    const {
      color,
      textAlign,
      backgroundColor,
      fontSize,
      lineHeight,
      borderColor,
      borderRadius,
      borderWidth,
      text,
      disabled
    } = this

    const style = {
      color,
      textAlign,
      backgroundColor,
      fontSize: fontSize,
      lineHeight: lineHeight + 'em',
      borderColor,
      borderRadius: borderRadius + 'px',
      borderWidth: borderWidth + 'px',
      textDecoration: 'none',
      disabled
    }
    return (
      <button
        style={style}
        onClick={this.handleClick}
      >{text}</button>)
  },
  name: 'lbp-form-button',
  props: {
    text: PropTypes.string({ defaultValue: 'æäº¤' }),
    vertical: PropTypes.boolean(),
    backgroundColor: PropTypes.color({ label: 'èæ¯è²', defaultValue: 'rgba(255, 255, 255, 0.2)' }),
    color: PropTypes.color(),
    fontSize: PropTypes.number({ label: 'å­å·(px)', defaultValue: 14 }),
    lineHeight: PropTypes.number({ label: 'è¡é«(px)', defaultValue: 1 }),
    borderWidth: PropTypes.number({ label: 'è¾¹æ¡å®½åº¦(px)', defaultValue: 1 }),
    borderRadius: PropTypes.number({ label: 'åè§(px)', defaultValue: 4 }),
    borderColor: PropTypes.color({ label: 'è¾¹æ¡é¢è²', defaultValue: '#ced4da' }),
    textAlign: PropTypes.textAlign(),
    disabled: PropTypes.boolean()
  },
  methods: {
    handleClick () {
      if (this.disabled) return

      // #!zh: data-type=lbp-form-input å¨ lbp-form-input ç»ä»¶ä¸­å®ä¹
      const inputs = document.querySelectorAll("[data-type^='lbp-form-input']")
      if (!inputs.length) return
      const self = this
      const formData = new FormData()
      inputs.forEach(input => formData.append(input.dataset.uuid, input.value))
      const req = new XMLHttpRequest()
      req.onreadystatechange = function () {
        if (req.readyState === 4) {
          const message = req.status === 200 ? 'æäº¤æå' : 'æäº¤å¤±è´¥'
          self.$message.info(message)
        }
      }
      const workId = window.__work.id
      // TODO #!zh: å¯ä»¥å¨æéç½®è¡¨åæäº¤å°å
      req.open('post', `/works/form/submit/${workId}`, true)
      req.send(formData)
    }
  }
}
