import PropTypes from '@/utils/commom-props'
import { quillEditor } from 'vue-quill-editor'
// require styles
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import './text-overwrite-quil-snow-theme.scss'
// https://github.com/luban-h5-components/plugin-common-props

export default {
  render (h) {
    const canEdit = this.canEdit && this.editorMode === 'edit'
    const style = {
      position: 'relative',
      color: `${this.color} !important`,
      textDecoration: 'none',
      backgroundColor: this.backgroundColor || 'rgba(255, 255, 255, 0.2)',
      lineHeight: `${this.lineHeight}em`,
      border: `${this.borderWidth}px solid ${this.borderColor}`,
      borderRadius: `${this.borderRadius}px`
    }
    /**
     * https://github.com/ly525/luban-h5/issues/155
     * éè¦ç»é¢è§æ¨¡å¼çæå­æ·»å  ql-snow æ ·å¼åå ï¼æå­æ ·å¼åæå­ç¼è¾å¨(ql-editor)ç theme æå³ç³»
     * æ¯å¦ç¼è¾æ¨¡å¼ h1 æ ·å¼ä¸ºï¼.ql-snow .ql-editor h1 {font-size: 2em;}
     * å æ­¤é¢è§æ¨¡å¼çæå­åå®¹ä¹éè¦å ä¸ div.ql-snow > div.ql-editor ä½ä¸ºwrapper
     */
    const previewText = <div class="ql-snow"><div domPropsInnerHTML={this.text} class="ql-editor ql-container"></div></div>
    return (
      <div
        onDblclick={e => {
          this.canEdit = true
          e.stopPropagation()
        }}
        onMousedown={e => {
          if (this.canEdit) { e.stopPropagation() }
        }}
        style={style}
        onKeydown={event => {
          const key = event.keyCode || event.charCode
          // #!en: backspace/delete key should only delete letter in textarea, do not delete element in canvas
          // #!zh: é®çå é¤ï¼åºè¯¥åªå é¤æå­ç»ä»¶éé¢çæå­ï¼èä¸æ¯å é¤ç»å¸ä¸çåç´ 
          if (key === 8 || key === 46) {
            event.stopPropagation()
          }
        }}
      >
        {
          canEdit
            ? <quillEditor
              content={this.text}
              options={{
                modules: {
                  // toolbar: '#toolbar-wrapper'
                  toolbar: [
                    ['bold', 'italic', 'underline', 'strike'], // åæ¢æé®
                    [{ list: 'ordered' }, { list: 'bullet' }],
                    [{ color: [] }, { background: [] }], // ä¸»é¢é»è®¤ä¸æï¼ä½¿ç¨ä¸»é¢æä¾çå¼
                    [{ align: [] }],
                    ['clean'], // æ¸é¤æ ¼å¼
                    [{ header: [1, 2, 3, 4, 5, 6, false] }]
                    // https://github.com/quilljs/quill/issues/1208
                  ]
                },
                theme: 'snow'
              }}
              onChange={({ quill, html, text }) => {
                this.$emit('input', {
                  value: html,
                  pluginName: 'lbp-text'
                })
              }}>
            </quillEditor>
            : previewText
        }
      </div>
    )
  },
  name: 'lbp-text',
  data () {
    return {
      canEdit: false,
      innerText: this.text || 'åå»ä¿®æ¹æå­'
    }
  },
  props: {
    backgroundColor: PropTypes.color({ label: 'èæ¯è²', defaultValue: 'rgba(0, 0, 0, 0)' }),
    borderWidth: PropTypes.number({ label: 'è¾¹æ¡å®½åº¦(px)', defaultValue: 0 }),
    borderRadius: PropTypes.number({ label: 'åè§(px)' }),
    borderColor: PropTypes.color({ label: 'è¾¹æ¡é¢è²' }),
    text: PropTypes.string({ label: 'åå®¹', defaultValue: 'åå»ä¿®æ¹æå­', visible: false }),
    editorMode: PropTypes.string({
      defaultValue: 'preview', // å¯éå¼: preview/edit
      label: 'æ¨¡å¼',
      visible: false
    })
  },
  editorConfig: {
  }
}
