import PropTypes from '@/utils/commom-props'
import './index.scss'

export default {
  name: 'lbp-bg-music',
  props: {
    disabled: PropTypes.boolean({
      defaultValue: true,
      label: 'disabled'
    }),
    autoplay: PropTypes.boolean({
      defaultValue: true,
      label: 'èªå¨æ­æ¾'
    }),
    src: PropTypes.string({
      label: 'é³ä¹URL',
      defaultValue: 'http://go.163.com/2018/0209/mengniu/audio/bgm.mp3',
      props: {
        type: 'textarea'
      }
    })
  },
  data: () => ({
    isPlaying: true
  }),
  methods: {
    toggle () {
      const bgAudio = this.$refs.bgAudio
      if (!bgAudio) return

      this.isPlaying ? bgAudio.pause() : bgAudio.play()
      this.isPlaying = !this.isPlaying
    }
  },
  render () {
    const btnStyle = {
      'animation-play-state': this.isPlaying ? 'running' : 'paused'
    }
    return (
      <div class="bg-music-wrapper" style="display: block;">
        <div class="bg-music-btn rotate" style={btnStyle} onClick={this.toggle} disabled={this.disabled}>
          <audio src={this.src} autoplay={this.autoplay} preload loop ref='bgAudio'></audio>
        </div>
      </div>
    )
  },
  created () {
    // å¨åå§åçæ¶åï¼autoplay æ§å¶æ¯å¦æ­æ¾
    // åé¢æ¯å¦æ­æ¾ï¼ç±ç¨æ·çç¹å»è¡ä¸ºå³å®
    this.isPlaying = this.autoplay
  }
}
