    labelContent: PropTypes.string({ label: 'å°ååç§°', defaultValue: '' }), // æ ç­¾åå®¹
    zoomLevel: PropTypes.number({ label: 'ç¼©æ¾å±çº§', defaultValue: 12, visible: false }),
    // https://lbs.qq.com/dev/console/key/manage
    qqMapKey: PropTypes.string({
