          <div>2. é²ç­ç Demo Key éæ¶å¯è½å¤±æï¼å¤±ææç¤º: é´æå¤±è´¥ï¼è¯·ä¼ å¥æ­£ç¡®çkey</div>
        </div>
      }
    }),
    poi: {
      type: Object,
      default: () => ({
        'latLng': {
          'lat': 39.90469,
          'lng': 116.40717
        },
        'name': 'åäº¬å¸',
        'type': 4
      }),
      editor: {
        custom: true
      }
    }
  },
  watch: {
    poi: {
      handler (poi) {
        if (!this.checkMapAvailable()) return
        this.setMarker(poi)
      },
      deep: true
    },
    labelContent (labelContent) {
      if (!this.checkMapAvailable()) return
      this.setLabel(labelContent)
