    setLabel (labelContent) {
      const center = Map.getPosition(this.poi.latLng) // å°å¾çä¸­å¿å°çåæ 
      this.label = this.label || new window.qq.maps.Label({
        position: center,
        map: this.map,
        content: ''
      })
      if (labelContent.trim()) {
        // https://lbs.qq.com/webDemoCenter/javascriptV2/marker/label
        this.label.setVisible(true)
        this.label.setContent(labelContent || '')
        this.label.setPosition(center)
      } else {
        this.label.setVisible(false)
      }
    },
    setZoomLevel (zoomLevel) {
      this.map.zoomTo(zoomLevel)
    },
    init () {
      const { poi, qqMapKey } = this
      this.loadMap(qqMapKey).then(qq => {
        this.initMap(poi)
        this.setLabel(this.labelContent)
        this.setMarker(poi)
      })
    },
    initMap (poi) {
      const el = this.$refs.mapElement
      const center = Map.getPosition(poi.latLng) // å°å¾çä¸­å¿å°çåæ 
      this.map = new window.qq.maps.Map(el, {
        center,
        zoom: this.zoomLevel, // è®¾ç½®å°å¾çç¼©æ¾çº§å«
        disableDefaultUI: true, // ç¦æ­¢æææ§ä»¶
        draggable: false, // è®¾ç½®æ¯å¦å¯ä»¥ææ½
        scrollwheel: false, // è®¾ç½®æ¯å¦å¯ä»¥æ»å¨
        disableDoubleClickZoom: true // è®¾ç½®æ¯å¦å¯ä»¥åå»æ¾å¤§
        // è®¾ç½®å°å¾æ ·å¼è¯¦æåè§MapType
      })
