import PropTypes from '@/utils/commom-props'
import LbpFormRadio from '../lbp-form-radio'

function getDefaultItems () {
  // defaultItems.slice(0)[0] === defaultItems.slice(0)[0] -> true
  // Object.assign(defaultItems)[0] === Object.assign(defaultItems)[0] -> true
  // clone = (val) => JSON.parse(JSON.stringify(val))
  // clone(defaultItems)[0] === clone(defaultItems)[0] -> false
  const defaultItems = [
    {
      value: 'éé¡¹A'
    },
    {
      value: 'éé¡¹B'
    },
    {
      value: 'éé¡¹C'
    }
  ]

  return defaultItems
}

export default {
  name: 'lbp-form-checkbox-group',
  components: {
    LbpFormRadio
  },
  props: {
    width: {
      default: 120
    },
    height: {
      default: 120
    },
    aliasName: PropTypes.string({
      defaultValue: 'æ é¢æ¼ç¤º',
      label: 'å¡«åæ é¢'
    }),
    items: PropTypes.textOptions({
      label: 'éé¡¹åè¡¨',
      defaultValue: () => getDefaultItems()
    }),
    // TODO æ½ç¦» radio-group è³ common-props
    type: {
      type: String,
      default: 'checkbox',
      editor: {
        type: 'a-radio-group',
        label: 'éæ©æ¨¡å¼',
        require: true,
        props: {
          options: [
            { label: 'åé', value: 'radio' },
            { label: 'å¤é', value: 'checkbox' }
          ],
          name: 'mode'
        }
      }
    }
  },
  data () {
    return {
      value: this.type === 'radio' ? '' : [],
      uuid: undefined
    }
  },
  computed: {
    value_ () {
      if (this.type === 'radio') {
        return this.value
      } else {
        const value = (Array.isArray(this.value) && this.value) || []
        return value.join(',')
      }
    }
  },
  watch: {
    type (type) {
      this.value = type === 'radio' ? '' : []
    }
  },
  mounted () {
    this.uuid = this.$el.dataset.uuid
  },
  methods: {
    /**
     * @param {String, Number} val radioValue or checkboxValue
     */
    onChange (val) {
      switch (this.type) {
        case 'radio':
          this.toggleRadio(val)
          break
        case 'checkbox':
          this.toggleCheckbox(val)
          break
        default:
          break
      }
    },
    toggleCheckbox (val) {
      const index = this.value.indexOf(val)
      if (index === -1) {
        this.value.push(val)
      } else {
        this.value.splice(index, 1)
      }
    },
    toggleRadio (val) {
      this.value = val
    }
  },
  render () {
    return (
      <div>
        <h3>{this.aliasName}</h3>
        <input
          type="text"
          hidden
          value={this.value_}
          data-type="lbp-form-input"
          data-uuid={this.uuid}
        />
        {this.items.map(item => (
          <lbp-form-radio
            vertical
            value={item.value}
            checked={
              this.type === 'radio'
                ? this.value === item.value
                : this.value.includes(item.value)
            }
            aliasName={this.uuid}
            type={this.type}
            onChange={this.onChange}
          >
            {item.value}
          </lbp-form-radio>
        ))}
      </div>
    )
  }
}
